import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { EvaluationsService } from '../../services/evaluations.service';
import { AuthService } from '../../services/auth.service';
import { CampsService } from '../../services/camps.service';
import { SwimService } from '../../services/swim.service';


import * as pdfMake from 'pdfmake/build/pdfmake.js';
import * as pdfFonts from 'pdfmake/build/vfs_fonts.js';
pdfMake.vfs = pdfFonts.pdfMake.vfs;

@Component({
  selector: 'app-swim-report',
  templateUrl: './swim-report.component.html',
  styleUrls: ['./swim-report.component.css']
})
export class SwimReportComponent implements OnInit {
  camperId;
  swimGroupId;
  campId;
  camper;
  swimGroup;
  lifeguard;
  loading;
  level;
  report;

  constructor(
    private route: ActivatedRoute,
    private evaluationsService: EvaluationsService,
    private authService: AuthService,
    private campsService: CampsService,
    private swimService: SwimService,
    private router: Router,
  ) {}

  loadReport(){
    this.loading = true;
    this.swimService.getSwimGroupGuest(this.campId,this.swimGroupId).subscribe(data => {
      this.swimGroup = data.group.data;
      this.lifeguard = data.group.lifeguard;
      for(let c of data.group.campers){
        if(c._id==this.camperId){
          this.camper = c;
          break;
        }
      }
      if(this.level == -1){
        this.report = this.camper.cSwimOpts.currentLevel;
        this.loading = false;
      }
      else{
        for(let lev of this.camper.cSwimOpts.completedLevels){
          if(lev.rcLevel == this.level){
            this.report = lev;
            break;
          }
        }
      }
      console.log(this.report);
      this.loading = false;
    });
  }

  completedSkills(level){
    var skills = [];
    var count = 1;
    for(let animal of level.animals){
      for(let skill of animal.skills){
        skill.num = count;
        count++;
        if(skill.completed)
          skills.push(skill);
      }
    }
    for(let skill of level.exitSkills){
      skill.num = count;
      count++;
      if(skill.completed)
        skills.push(skill);
    }
    return skills;
  }

  generateSkillClass(level,skill){
    return "skill-"+level+"-"+skill.num;
  }

  displayDate(){
    var d = new Date()
    return d.getMonth()+1 + "/" + d.getDate() + "/" + d.getFullYear();
  }

  downloadPDF(){
    var swimLevel = this.camper.cSwimOpts.currentLevel;
    var docDefinition = { 
      content: [],
    pageSize: {width:700,height:1000},
    pageMargins: [0,0]
    };
    docDefinition.content.push({
      image: this.base64Levels(swimLevel.rcLevel),
			width: 700
    });
    docDefinition.content.push({
      text: this.displayDate(),
      absolutePosition: {x:swimLevel.pdfLocs.date.x,y:swimLevel.pdfLocs.date.y},
      color: "black",
      bold:true
    });
    docDefinition.content.push({
      text: this.camper.first+" "+this.camper.last,
      absolutePosition: {x:swimLevel.pdfLocs.name.x,y:swimLevel.pdfLocs.name.y},
      color: "black",
      bold:true
    });
    if(this.lifeguard){
      docDefinition.content.push({
        text: this.lifeguard.first+" "+this.lifeguard.last,
        absolutePosition: {x:swimLevel.pdfLocs.instructor.x,y:swimLevel.pdfLocs.instructor.y},
        color: "black",
        bold:true
      });
    }
    docDefinition.content.push({
      text: this.swimGroup.name,
      absolutePosition: {x:swimLevel.pdfLocs.group.x,y:swimLevel.pdfLocs.group.y},
      color: "black",
      bold:true
    });
    for(let skill of this.completedSkills(swimLevel)){
      if(skill.pdfLoc){
        var data = {
          text: "X",
          width: 50,
          absolutePosition: {x:skill.pdfLoc.x,y:skill.pdfLoc.y},
          color: "green",
          bold:true
        }
        docDefinition.content.push(data);
      }
    }
    for(let skill of swimLevel.exitSkills){
      if(skill.completed){
        if(skill.pdfLoc){
          var data = {
            text: "X",
            width: 50,
            absolutePosition: {x:skill.pdfLoc.x,y:skill.pdfLoc.y},
            color: "green",
            bold:true
          }
          docDefinition.content.push(data);
        }
      }
    }
    pdfMake.createPdf(docDefinition).download('swim_report.pdf');
  }

  ngOnInit() {
      this.route.paramMap.subscribe(params => {
        this.campId = params.get('campId');
        this.camperId = params.get('camperId');
        this.swimGroupId = params.get('swimGroupId');
        this.level = params.get('level');
        this.loadReport();
      });
  }

  checkImg(){
    return 'data:image/png;base64,'
  }

  base64Levels(num){
    switch(num){
      case 1:
        return 'data:image/jpeg;base64,';
      case 2:
        return 'data:image/jpeg;base64,';
      case 3:
        return 'data:image/jpeg;base64,';
      case 4:
        return 'data:image/jpeg;base64,';
      case 5:
        return 'data:image/jpeg;base64,';

    }
  }

}


